@import "main-bx";

.coupon,
.checkout_coupon {
    @include flex;
    align-items: center;
    float: left !important;
    width: 100%;
    max-width: 500px;
    input.input-text {
        width: 100% !important;
        max-width: 200px !important;
        margin: 0 20px 0 0 !important;
        height: 45px !important;
        border-radius: 0 !important;
        border: 1px solid $grey_border-color !important;
        padding-left: 20px !important;
        @include to($mobile) {
            margin-bottom: 20px !important;
        }
    }
    button.button {
        background-color: $orange-color !important;
        color: $white-color !important;
        height: 35px !important;
        padding: 0 !important;
        color: $white-color !important;
        height: 45px !important;
        max-width: 200px;
        width: 100%;
        @include transition;
        border-radius: 0 !important;
        margin: 0 !important;
        &:hover {
            background-color: $blue_hover-color;
            color: $white-color;
        }
    }
    @include to($mobile) {
        input {
            max-width: 100% !important;
            margin: 0 0 20px !important;
        }
        button {
            width: 100% !important;
            margin: 0 0 20px !important;
        }
    }
}

.checkout_coupon {
    max-width: 100% !important;
    margin: 0 0 30px !important;
    border-radius: 0 !important;
    .form-row.form-row-first {
        width: 100%;
        max-width: 250px;
        @include to($mobile) {
            max-width: 100%;
            input {
                max-width: 100% !important;
            }
        }
    }
    .form-row.form-row-last {
        @include to($mobile) {
            button.button {
                max-width: 100%;
            }
        }
    }
    p {
        font-size: 16px;
        width: 100%;
        margin-bottom: 15px;
    }
}

.woocommerce table.cart td.actions {
    & > button.button {
        background-color: $orange-color !important;
        color: $white-color !important;
        height: 35px !important;
        padding: 0 !important;
        color: $white-color !important;
        height: 45px !important;
        max-width: 200px;
        width: 100%;
        border-radius: 0 !important;
        @include transition;
        border: 1px solid $orange-color;
        &:hover {
            background-color: $blue_hover-color;
            color: $white-color;
        }
    }
}

.checkout-button,
.place-order button {
    background-color: $orange-color !important;
    border: 1px solid $orange-color !important;
    color: $white-color !important;
    @include transition;
    margin-top: 20px !important;
    border-radius: 0 !important;
    &:hover {
        background-color: $white-color !important;
        color: $orange-color !important;
    }
    @include to($mobile) {
        width: 100%;
        text-align: center;
        margin-bottom: 20px !important;
    }
}
.woocommerce-cart-form {
    margin-top: 1.85em;
    .product-thumbnail {
        @include to($mobile) {
            display: block !important;
            img {
                margin: -20px 0 0;
                max-width: 100%;
            }
        }
    }
    img {
        display: block;
        width: 100%;
        max-width: 130px;
    }
    .woocommerce-Price-amount {
        color: $orange-color;
        font-size: 24px;
        font-family: $font-bold;
    }
    @include to($mobile) {
        .woocommerce table.shop_table_responsive tr td::before,
        .woocommerce-page table.shop_table_responsive tr td::before {
            content: none !important;
        }
    }
}
.woocommerce-checkout {
    .shop_table {
        background-color: transparent !important;
    }
}

.wgm-tax {
    color: $dark_red_color;
    font-size: 16px;
    .woocommerce-Price-amount {
        color: $dark_red_color;
        font-size: 16px;
    }
}
.jde_hint {
    a {
        color: $orange-color;
        font-family: $font-bold;
    }
}
.woocommerce table.shop_table {
    border-radius: none !important;
}
.woocommerce ul#shipping_method li {
    display: flex;
    align-items: center;
    label {
        cursor: pointer;
    }
}
.woocommerce form {
    .form-row-last,
    .form-row-first {
        width: 100%;
    }
}
form.checkout {
    h3 {
        text-transform: uppercase;
        margin-bottom: 30px;
    }
    .order-btn-submit {
        display: block;
        margin: 0 0 1rem 0;
        padding: 0.85em 1em;
        border: 1px solid transparent;
        border-radius: 2px;
        font-size: 18px;
        line-height: 1;
        text-align: center;
        cursor: pointer;
        background-color: $orange-color;
        color: $white-color;
        @include transition;
        border: 1px solid $orange-color;
        &:hover {
            background-color: $white-color;
            color: $orange-color;
        }
    }
}
#customer_details {
    margin-bottom: 30px;
}
.woocommerce-MyAccount-navigation {
    margin-bottom: 30px;
    ul {
        @include flex;
        align-items: center;
        justify-content: space-between;
        @include to($mobile) {
            justify-content: space-evenly;
        }
    }
    a {
        text-decoration: underline;
        color: $orange-color;
        @include transition;
        &:hover {
            text-decoration: none;
        }
    }
}
button {
    &[name="save_account_details"] {
        margin-top: 30px !important;
        @include transition;
        border: 1px solid $orange-color !important;
        &:hover {
            background-color: $white-color !important;
            color: $orange-color !important;
        }
    }
}
.show-password-input {
    top: 5px !important;
    right: 5px !important;
}
.woocommerce table.shop_table_responsive tr td::before {
    float: none !important;
}
.woocommerce table.shop_table_responsive tr:nth-child(2n) td,
.woocommerce-page table.shop_table_responsive tr:nth-child(2n) td {
    background-color: transparent !important;
}
.select2-container--default .select2-selection--single {
    border: 1px solid #818181;
    font-size: 1rem;
    color: rgba(0, 0, 0, 0.6);
    border-radius: 2px;
    padding: 0.3em 0 0.5em;
    height: 39px;
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
    height: 39px;
}
div.asl_m .probox .proclose svg {
    background: transparent !important;
}
.woocommerce-EditAccountForm.edit-account {
    p {
        &:nth-child(1),
        &:nth-child(2) {
            width: 49% !important;
            @include to($mobile) {
                width: 100% !important;
            }
        }
    }
}
.woocommerce-MyAccount-content {
    p,
    h3 {
        margin-bottom: 15px;
    }
    address {
        margin-top: 15px;
    }
}
.woocommerce .woocommerce-customer-details address {
    min-height: 169px;
}
.woocommerce table.my_account_orders {
    a.button {
        margin: 20px 0;
        @include transition;
        border: 1px solid $orange-color !important;
        &:hover {
            background-color: $white-color;
            color: $orange-color;
        }
    }
}
a.button.wc-backward {
    @include transition;
    border: 1px solid $orange-color !important;
    max-width: 200px !important;
    display: block !important;
    &:hover {
        background-color: $white-color;
        color: $orange-color;
    }
}
